import Header from './header';
import styled, { ThemeProvider } from 'styled-components';

const theme = {
  shadowColor: 'rgba(0, 0, 0, 0.03)',
  classicShadow: '6px 6px 20px rgba(0, 0, 0, 0.03)'
};

const Layout = props => (
  <ThemeProvider theme={theme}>
    <>
      <Header />
      {props.children}
    </>
  </ThemeProvider>
);

export default Layout;
